import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex);

// 
const state = {
    count: 10,
    editForm: [],
    infoId: 0,
    type: [
        {value: '1', label: '', url:'activity'},
        {value: '2', label: '', url:'new'},
        {value: '3', label: '', url:'knowledge'},
        {value: '4', label: '', url:'team'},
        {value: '5', label: '', url:'alumni'},
        {value: '6', label: '', url:'tutor'},
        {value: '7', label: '', url:'train'},
        {value: '8', label: '', url:'policy'},
        {value: '9', label: '', url:'fund'},
        {value: '10', label: '', url:'enter'},
        {value: '11', label: '', url:'manage'},
        {value: '12', label: '', url:'assess'},
        {value: '13', label: '', url:'hatch'}]
};

//  mutations
const mutations = {
    INCREMENT(state) {
        state.count++
    },
    DECREMENT(state) {
        state.count--
    },
    EDIT_INFO(state, id) {
        // state.editForm = arr;
        state.infoId = id;
    }
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


